import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { ServiceHandimanComponent } from './service-handiman/service-handiman.component';
import { ServiceComputerComponent } from './service-computer/service-computer.component';
import { ServiceElectricalComponent } from './service-electrical/service-electrical.component';
import { ServiceElectronicsComponent } from './service-electronics/service-electronics.component';
import { ServiceFashionComponent } from './service-fashion/service-fashion.component';
import { ServiceHomeAppliancesInstallationComponent } from './service-home-appliances-installation/service-home-appliances-installation.component';
import { ServiceHomeAppliancesRepairingComponent } from './service-home-appliances-repairing/service-home-appliances-repairing.component';
import { ServiceLaundryComponent } from './service-laundry/service-laundry.component';
import { ServicePlumbingComponent } from './service-plumbing/service-plumbing.component';
import { ServiceSmartHomeHubSetupComponent } from './service-smart-home-hub-setup/service-smart-home-hub-setup.component';
const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'handimanService', component: ServiceHandimanComponent },
  { path: 'computerService', component: ServiceComputerComponent },
  { path: 'electricalService', component: ServiceElectricalComponent },
  { path: 'electronicsService', component: ServiceElectronicsComponent },
  { path: 'fashionService', component: ServiceFashionComponent },
  { path: 'homeAppliancesInstallationsService', component: ServiceFashionComponent },
  { path: 'homeAppliancesReparingService', component: ServiceHomeAppliancesRepairingComponent },
  { path: 'laundryService', component: ServiceLaundryComponent },
  { path: 'plumbingService', component: ServicePlumbingComponent },
  { path: 'smartHomeSetupService', component: ServiceSmartHomeHubSetupComponent },
  { path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes), NgbModule],
  declarations: [
    HomeComponent,
    AboutComponent,
    ServiceHandimanComponent,
    ServiceComputerComponent,
    ServiceElectricalComponent,
    ServiceElectronicsComponent,
    ServiceFashionComponent,
    ServiceHomeAppliancesInstallationComponent,
    ServiceHomeAppliancesRepairingComponent,
    ServiceLaundryComponent,
    ServicePlumbingComponent,
    ServiceSmartHomeHubSetupComponent,
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
